import { NextResponse } from 'next/server';
import connectDB from '@/lib/db';
import Property from '@/models/property';
import { authMiddleware } from '@/middleware/authMiddleware';

// Get all properties
export async function GET(request) {
  try {
    await connectDB();
    
    const properties = await Property.find()
      .populate('owner', 'name email')
      .sort({ createdAt: -1 });
    
    return NextResponse.json({ properties });
  } catch (error) {
    console.error('Error fetching properties:', error);
    return NextResponse.json(
      { error: 'Error fetching properties' },
      { status: 500 }
    );
  }
}

// Create a new property
export async function POST(request) {
  try {
    // Check authentication
    const authenticatedRequest = await authMiddleware(request);
    
    // If the response has a status property, it means there was an auth error
    if ('status' in authenticatedRequest) {
      return authenticatedRequest;
    }

    await connectDB();
    
    // Parse the request body
    const body = await request.json();
    
    // Create the property data
    const propertyData = {
      ...body,
      owner: authenticatedRequest.user.id,
      images: body.images?.length ? body.images : ['https://via.placeholder.com/400x300']
    };

    console.log('Creating property with data:', propertyData);

    const property = await Property.create(propertyData);
    
    return NextResponse.json({ property }, { status: 201 });
  } catch (error) {
    console.error('Error creating property:', error);
    return NextResponse.json(
      { error: 'Error creating property: ' + error.message },
      { status: 500 }
    );
  }
}
